@font-face {
  font-family: "Qanelas";
  src: url("/assets/fonts/Qanelas-Regular.eot");
  src: url("/assets/fonts/Qanelas-Regular.woff") format("woff"), url("/assets/fonts/Qanelas-Regular.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "Qanelas";
  src: url("/assets/fonts/Gilroy-Bold.eot");
  src: url("/assets/fonts/Qanelas-Bold.woff") format("woff"), url("/assets/fonts/Qanelas-Bold.ttf") format("truetype");
  font-weight: 700;
  font-style: normal;
}
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  font-size: 100%;
  vertical-align: baseline;
}

a {
  text-decoration: none;
}
a:active, a:hover {
  outline: 0;
}

ul, li {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

h1, h2, h3, h4, h5, h6 {
  font-size: 100%;
  font-weight: normal;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: border-box;
}

:focus {
  outline: 0;
}

img, audio, video {
  max-width: 100%;
  height: auto;
}

audio, canvas, iframe, video, img, svg {
  vertical-align: middle;
}

iframe {
  border: 0;
}

textarea {
  resize: none;
  /*remove the resize handle on the bottom right*/
  overflow: auto;
  vertical-align: top;
  box-shadow: none;
  -webkit-box-shadow: none;
  -moz-box-shadow: none;
}

input, textarea, select, button {
  outline: none;
  border: none;
  font-size: 100%;
  margin: 0;
}

button, input {
  line-height: normal;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

td, th {
  padding: 0;
  text-align: left;
}

/** @format */
.header {
  width: 100%;
  position: fixed;
  background-color: white;
  z-index: 10;
}
.header:before, .header:after {
  content: "";
  position: absolute;
  width: 1px;
  height: 100%;
  top: 20px;
  margin-top: -20px;
  background: #E9E9E9;
  z-index: -1;
}
.header:before {
  left: 35.3%;
}
.header:after {
  right: 35.3%;
}
.header .container {
  padding-bottom: 15px;
  padding-top: 12px;
  display: grid;
  grid-template-columns: auto 1fr auto;
  grid-gap: 80px;
  position: relative;
}
.header .container:after {
  display: none;
}
.header .button-type-one {
  padding: 12px 20px;
  align-self: center;
}
.header__mob {
  display: none;
  margin: auto 0 0;
}
.header__mob .button-type-none {
  z-index: 2;
  position: relative;
  height: 16px;
  width: 32px;
}
.header__mob .button-type-none span:after,
.header__mob .button-type-none span:before {
  content: "";
  position: absolute;
  background: #2c287d;
  display: block;
  border-radius: 1px;
  right: 0;
  top: 0;
  height: 2px;
  width: 32px;
  transition: 0.4s cubic-bezier(0.68, -0.6, 0.32, 1.6);
}
.header__mob .button-type-none span:before {
  top: 12px;
}
.header__mob__modal {
  position: absolute;
  top: 0;
  right: -800px;
  background-color: white;
  width: 100%;
  height: 100vh;
  z-index: 1;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.header__mob__modal .button-type-one {
  display: block;
}
.header__mob__modal .logo {
  width: 112px;
  margin-top: 12px;
  margin-left: 20px;
}
.header__mob__modal .nav {
  text-align: center;
}
.header__mob__modal .nav li {
  margin-bottom: 30px;
}
.header__mob__modal .nav .link {
  color: #2c287d;
  font-weight: 600;
  font-size: 28px;
  line-height: 34px;
}
.header__mob__modal .nav .link:hover {
  color: #ffffff !important;
  transition: 0.8s;
  background: #2c287d;
}
.header__mob__modal .button-type-one {
  margin: 10px auto 0;
  padding: 15px 30px !important;
}

.nav-atc span::after {
  transform: rotate(-45deg);
  top: 25% !important;
}
.nav-atc span::before {
  transform: rotate(45deg);
  top: 25% !important;
}

.header .container.mod:after {
  transition: all 0.2s linear;
  z-index: -1;
}

.nav-modal-act {
  transition: all 0.2s linear;
  right: 0;
}

.links {
  display: flex;
  align-items: flex-end;
  gap: 40px;
}
.links li {
  position: relative;
  z-index: 1;
}
.links li:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  background: #2c287d;
  transition: all 0.1s ease-out;
}
.links li:hover:before {
  transition: all 0.5s ease-out;
  width: 100%;
}
.links li:hover .link {
  color: #ffffff;
  transition: all 0.7s ease-out;
}

.link {
  line-height: 19px;
  padding: 2px 8px;
  position: relative;
  height: 100%;
  z-index: 2;
}

.contacts-mod:before {
  display: none;
}
.contacts-mod:after {
  left: 50%;
}

@media (max-width: 1900px) {
  .header:before {
    left: 33.3%;
  }
  .header:after {
    right: 33.3%;
  }
}
@media (max-width: 1255px) {
  .links {
    gap: 15px;
  }

  .header .container {
    gap: 35px;
  }
}
@media (max-width: 1000px) {
  .links {
    gap: 10px;
  }

  .header .container {
    gap: 15px;
  }
}
@media (max-width: 940px) {
  .links {
    gap: 5px;
  }

  .header .container {
    gap: 5px;
  }

  .header .container {
    grid-template-columns: 120px 1fr auto;
  }

  .header .button-type-one {
    padding: 12px 10px;
  }
}
@media (max-width: 820px) {
  .links {
    gap: 0;
  }

  .header .container {
    gap: 2px;
  }

  .header .container {
    grid-template-columns: 100px 1fr auto;
  }

  .header .button-type-one {
    padding: 10px 5px;
  }
}
@media (max-width: 768px) {
  .links {
    display: none;
  }

  .logo {
    width: 113px;
    z-index: 2;
  }

  .header:before, .header:after {
    display: none;
  }
  .header .container {
    display: flex;
    justify-content: space-between;
    padding-bottom: 20px;
  }
  .header .container:after {
    display: block;
    z-index: 1;
    top: 20px;
  }
  .header .button-type-one {
    display: none;
  }
  .header__mob {
    display: flex;
  }
  .header__mob__modal .button-type-one {
    display: block;
  }
}
/** @format */
.footer {
  background-color: #f3f4f6;
}
.footer .container {
  padding-top: 30px;
  padding-bottom: 30px;
}
.footer .container::before {
  display: none;
}
.footer .container::after {
  display: none;
}
.footer__top {
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #D8D8D8;
  padding-bottom: 30px;
}
.footer__bottom {
  padding-top: 30px;
  display: flex;
  justify-content: space-between;
}
.footer__bottom__wrap {
  display: flex;
  flex-direction: column;
  gap: 4px;
}
.footer__bottom__wrap a:hover {
  background: transparent;
  color: black;
}
.footer__bottom__value {
  line-height: 24px;
  color: black;
}
.footer__bottom__description {
  line-height: 24px;
  color: #919193;
}

@media (max-width: 1000px) {
  .footer__bottom {
    display: grid;
    grid-template-columns: 50% 50%;
    grid-gap: 30px;
  }
}
@media (max-width: 768px) {
  .footer__top {
    text-align: center;
    display: flex;
    flex-direction: column;
  }
  .footer__top .links {
    display: flex;
    flex-direction: column;
    gap: 24px;
    align-items: center;
  }
  .footer__top .logo {
    margin: 0 auto;
    margin-bottom: 30px;
  }

  .footer__bottom {
    text-align: center;
    display: flex;
    flex-direction: column;
  }
}
/** @format */
.about-us img {
  border-radius: 16px;
}
.about-us .container {
  margin-top: 128px;
  margin-bottom: 63px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 0 120px;
}
.about-us__statistics {
  display: grid;
  grid-auto-rows: max-content;
  grid-gap: 40px 0;
}
.about-us__statistics__title {
  font-weight: 600;
  font-size: 48px;
  line-height: 56px;
  color: #2c287d;
}
.about-us__statistics__parasite {
  display: grid;
  grid-gap: 40px;
}
.about-us__statistics__parasite__block {
  display: flex;
  gap: 20px;
}
.about-us__statistics__cards {
  display: grid;
  grid-template-rows: repeat(3, 1fr);
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 20px;
}
.about-us__statistics__card {
  background: #f3f4f6;
  text-align: center;
  padding: 12px 0;
  border-radius: 12px;
}
.about-us__statistics__card :first-child {
  color: #2c287d;
  font-weight: bold;
  font-size: 20px;
  line-height: 25px;
}
.about-us__statistics__card :last-child {
  font-size: 16px;
  line-height: 19px;
}
.about-us__statistics__card .wrap {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 3px;
}
.about-us__statistics__card .wrap :last-child {
  margin-top: 2px;
}
.about-us__history p {
  margin-top: 34px;
}
.about-us__group-compani img {
  border-radius: 0;
}
.about-us__group-compani__title {
  font-weight: 600;
  font-size: 24px;
  line-height: 30px;
  margin-bottom: 20px;
}
.about-us__group-compani__cards {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  grid-template-columns: repeat(2, 1fr);
  grid-row-gap: 20px;
  grid-column-gap: 40px;
}
.about-us__group-compani__card img {
  margin-bottom: 20px;
}
.about-us__group-compani__card img.logo {
  margin-bottom: 12px;
  border: 1px solid transparent;
}
.about-us__group-compani__card img.logo:hover {
  transition: 0.2s !important;
  -webkit-filter: drop-shadow(1px 3px 3px #5551A6);
  filter: drop-shadow(1px 3px 3px #5551A6);
}

@media (max-width: 1250px) {
  .about-us .container {
    grid-gap: 0 80px;
  }
  .about-us__statistics, .about-us__history {
    margin-top: 0;
  }
  .about-us__statistics__cards {
    grid-template-columns: repeat(2, 1fr);
  }

  .about-us__group-compani__cards {
    gap: 5px;
  }
}
@media (max-width: 1020px) {
  .about-us .container {
    grid-gap: 0 40px;
  }
}
@media (max-width: 900px) {
  .about-us .container {
    grid-template-columns: 1fr 1fr;
    grid-template-areas: "s h" "g g";
    grid-gap: 20px 40px;
  }
  .about-us__statistics {
    grid-area: s;
  }
  .about-us__history {
    grid-area: h;
  }
  .about-us__group-compani {
    grid-area: g;
  }
  .about-us__group-compani__card {
    display: flex;
    flex-direction: column;
  }
}
@media (max-width: 768px) {
  .about-us img {
    border-radius: 12px;
  }
  .about-us .about-us__group-compani__cards {
    gap: 21px;
  }
  .about-us .container {
    display: flex;
    flex-direction: column;
    margin-top: 92px;
  }
  .about-us__history img {
    width: 100%;
  }
  .about-us__group-compani {
    margin-top: 30px;
  }
  .about-us__group-compani img {
    border-radius: 0;
  }
  .about-us__group-compani .ideal-logo {
    height: 48px;
    width: 36px;
  }
  .about-us__group-compani .tochka-logo {
    height: 24px;
    width: 79.2px;
  }
  .about-us__group-compani .fortuna-logo {
    height: 24px;
    width: 45px;
  }
  .about-us__group-compani .bon-buasson-logo {
    height: 48px;
    width: 55px;
  }
}
@media (max-width: 600px) {
  .about-us__statistics {
    gap: 30px 0;
  }
  .about-us__statistics__title {
    font-size: 36px;
    line-height: 40px;
  }
  .about-us__statistics__title-now {
    font-size: 17px;
    line-height: 20px;
  }
  .about-us__statistics__parasite {
    gap: 20px;
  }
  .about-us__statistics__card :first-child {
    font-size: 18px;
    line-height: 22px;
  }
  .about-us__statistics__card :last-child {
    font-size: 14px;
    line-height: 18px;
  }
  .about-us__history {
    margin-top: 20px;
  }
  .about-us__history p {
    margin-top: 20px;
    line-height: 22px;
  }
  .about-us__group-compani {
    margin-top: 30px;
  }
  .about-us__group-compani__title {
    font-size: 20px;
    line-height: 24px;
  }
  .about-us__group-compani__cards {
    grid-column-gap: 21px;
  }
}
/** @format */
.coverage-map {
  scroll-behavior: smooth;
  background-color: #f3f4f6;
}
.coverage-map .container {
  padding-top: 80px;
  position: relative;
}
.coverage-map .container:after, .coverage-map .container:before {
  display: none;
}
.coverage-map__map {
  position: absolute;
  right: 60px;
  top: 60px;
}
.coverage-map__map svg {
  width: 100%;
}
.coverage-map__info__title {
  font-weight: 600;
  font-size: 48px;
  line-height: 56px;
  color: #2c287d;
}
.coverage-map__info__title-now {
  margin-top: 20px;
}
.coverage-map__info__text {
  margin-top: 30px;
  display: flex;
}
.coverage-map__info__text p {
  font-weight: bold;
  font-size: 64px;
  line-height: 79px;
}
.coverage-map__info__text .wrap {
  display: flex;
}
.coverage-map__info__text .wrap :first-child {
  width: 70px;
}
.coverage-map__info__description {
  font-weight: bold;
  font-size: 20px;
  line-height: 24px;
  margin: auto 0 18px 25px;
  color: black;
}
.coverage-map__info__region {
  color: white;
}
.coverage-map__info__region p {
  -webkit-text-stroke: 1px #2c287d;
}
.coverage-map__info__purpose {
  color: #5551A6;
}
.coverage-map__info__region-now {
  color: #2c287d;
}
.coverage-map__info__cities {
  margin-top: 30px;
  margin-bottom: 60px;
}
.coverage-map__info__city {
  display: flex;
  margin-bottom: 20px;
}
.coverage-map__info__city p {
  margin-right: 40px;
}
.coverage-map__info__city p.odessa {
  grid-area: o;
}
.coverage-map__info__city span {
  font-weight: bold;
  color: #2c287d;
}
.coverage-map__info__city p.nikolaev {
  grid-area: n;
}
.coverage-map__info__city p.herson {
  grid-area: h;
}
.coverage-map__info__city p.dnepr {
  grid-area: d;
}
.coverage-map__info__city p.zaporiz {
  grid-area: z;
}
.coverage-map__info__city p.kropyv {
  grid-area: k;
}

