/** @format */
.brands {
  padding-top: 100px;
}
.brands .container {
  grid-template-areas: "title title title" "first second third";
}
.brands__block {
  display: grid;
  position: relative;
  grid-template-columns: 1fr 1fr;
  margin: 40px 0 100px;
  grid-gap: 45px 0;
  height: 285px;
}
.brands__block__img {
  margin: auto;
}
.brands__block__img:hover img {
  transition: 0.8s !important;
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}
.brands__block:first-of-type {
  grid-area: first;
  margin-left: -30px;
}
.brands__block:nth-of-type(2) {
  grid-area: second;
}
.brands__block:nth-of-type(3) {
  grid-area: third;
  margin-right: -30px;
}
.brands__title {
  grid-area: title;
  font-weight: 600;
  font-size: 48px;
  line-height: 56px;
  color: #2c287d;
}

@media (max-width: 1570px) {
  .brands__block__img {
    width: 80%;
  }
}
@media (max-width: 768px) {
  .brands {
    padding-top: 40px;
  }
  .brands .container {
    gap: 30px;
    margin-bottom: 40px;
    grid-template-areas: "title " "first" "second" "third";
  }
  .brands__block {
    margin: 0;
    column-gap: 40px;
  }
  .brands__block .brands__block__img {
    width: 100%;
    text-align: center;
  }
}
@media (max-width: 600px) {
  .brands__title {
    font-size: 34px;
    line-height: 40px;
    font-weight: 600;
  }
}
/** @format */
.logistics {
  background-color: #f3f4f6;
}
.logistics img {
  border-radius: 16px;
  height: 100%;
}
.logistics .container {
  grid-template-areas: "title title" "indicators chartMeter " "img chartTons ";
  grid-gap: 30px 70px;
  padding: 80px 60px 98px;
}
.logistics__title {
  grid-area: title;
  font-weight: 600;
