.modal_bg {
  width: 100%;
  height: 100%;
  backdrop-filter: blur(6px);
  min-height: 100vh;
  position: fixed;
  top: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 20;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s, opacity 0.3s;
}
.modal_bg.active {
  visibility: visible;
  opacity: 1;
}
.modal_bg .button-type-none {
  position: absolute;
  top: 40px;
  right: 40px;
}
.modal_bg .container::before, .modal_bg .container::after {
  display: none;
}

[data-aos=clip-right] {
  clip-path: polygon(0 0, 0 0, 0 100%, 0 100%);
  transition-property: clip-path, transform;
}
[data-aos=clip-right].aos-animate {
  clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
  opacity: 1;
}

[data-aos=clip-top] {
  clip-path: polygon(0 0, 100% 0, 100% 0, 0 0);
  transform: translateY(40px);
  transition-property: clip-path, transform;
}
[data-aos=clip-top].aos-animate {
  clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
  opacity: 1;
  transform: translateY(0px);
}

[data-aos=clip-down] {
  clip-path: polygon(0 100%, 100% 100%, 100% 100%, 0% 100%);
  transform: translateY(10px);
  transition-property: clip-path, transform;
}
[data-aos=clip-down].aos-animate {
  clip-path: polygon(0 100%, 100% 100%, 100% 0, 0 0);
  opacity: 1;
  transform: translateY(0px);
}

@media (max-width: 768px) {
  .modal_bg .button-type-none {
    position: absolute;
    top: -45px;
    opacity: 0.7;
    right: -5px;
    filter: grayscale(1) invert(1);
  }
  .modal_bg h2 {
    font-size: 24px;
    line-height: 32px;
  }
}
/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-touch-callout: none;
  -khtml-user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  display: block;
  overflow: hidden;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  top: 0;
  left: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.slick-track:before,
.slick-track:after {
  display: table;
  content: "";
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  display: none;
  float: left;
  height: 100%;
  min-height: 1px;
}

[dir=rtl] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

html {
  scroll-behavior: smooth;
}

body {
  padding: 0;
  font-family: "Qanelas", sans-serif;
  font-size: 16px;
  line-height: 24px;
  font-weight: normal;
  background-color: #ffffff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  position: relative;
}

*, *:before, *:after {
  box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

a {
  color: black;
  line-height: 19px;
}

ul, li {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.wrapper {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  min-height: 100vh;
  overflow: hidden;
}

.content-wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.container {
  width: 100%;
  display: grid;
  margin: 0 auto;
  max-width: 1680px;
  padding: 0 60px;
}
.container::before, .container::after {
  content: "";
  position: absolute;
  width: 1px;
  height: 100%;
  top: 0;
  margin-top: -20px;
  background: #E9E9E9;
  z-index: -1;
}
.container::before {
  left: 35.3%;
}
.container::after {
  right: 35.3%;
}

.header .container::before, .footer .container::before {
  display: none;
}

.button-type-one {
  font-weight: 600;
  line-height: 20px;
  color: #ffffff;
  text-align: center;
  padding: 15px 30px;
  background-color: #2c287d;
  border-radius: 6px;
  cursor: pointer;
  transition: all 0.4s ease-out;
}
.button-type-one:hover {
  background-color: #1a184b;
}

.button-type-none {
  background-color: transparent;
  cursor: pointer;
}

.input-type-one {
  padding: 14px 30px 16px 20px;
  border: 1px solid #D8D8D8;
  box-sizing: border-box;
  font-size: 16px;
  line-height: 19px;
  border-radius: 6px;
}

.lock {
  overflow: hidden;
}

.lock-burger {
  overflow: hidden;
}

.state {
  stroke: dodgerblue;
  transform-origin: center center;
  transform-box: fill-box;
  transition: 0.5s;
}

:hover.state {
  stroke: #2c287d;
  stroke-width: 3px;
  fill: #1a184b;
  z-index: 10000;
}

.dn {
  display: none;
}

.preloader {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
  background: #fff;
  z-index: 1001;
}

.preloader__image {
  position: relative;
  top: 50%;
  left: 50%;
  height: 120px;
  margin-top: -60px;
  margin-left: -180px;
}
.preloader__image img {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
}

.loaded_hiding .preloader {
  transition: 0.3s opacity;
  opacity: 0;
}

.loaded .preloader {
  display: none;
}

@media (max-width: 1900px) {
  .container::before {
